import { Preflight } from '@common/js/Preflight.js'
import { Main, MainBorder } from '@common/js/Build.js'
import { Animation } from '@common/js/Animation.js'
import { Core } from 'ad-control'
import { Control } from '@common/js/Control.js'
import { Styles, Markup, Align, Effects } from 'ad-view'

export class Ad {
  // called from index.html onImpression()
  static launch(binaryAssets) {
    console.log('Ad.launch()')
    Core.init(binaryAssets)
      .then(() => Preflight.init())
      .then(() => Core.loadDynamic())
      .then(() => Ad.prepare())
      .catch(err => {
        throw err
      })
  }

  static prepare() {
    console.log('Ad.prepare()')
    adData.preloaderScaleTo = 20
    Control.preMarkup()
    View.main = new Main({
      endframe: {
        css: {
          clip: 'rect(200px, 190px, 240px, 130px)'
        },
        frame: {
          borderRadius: 17,
          align: {
            x: {
              type: Align.CENTER
              // offset: -1
            },
            y: {
              type: Align.CENTER,
              offset: -8
            }
          },
          logo: {
            align: {
              x: {
                type: Align.RIGHT,
                offset: 6
              },
              y: {
                type: Align.BOTTOM,
                offset: 21
              }
            }
          },
          copy: {
            header: {
              css: {
                width: 300
              },
              fontSize: 24
            },
            sub: {
              css: {
                width: 300
              },
              // spacing: -0.25,
              fontSize: 17
            },
            align: {
              y: {
                type: Align.CENTER,
                against: 206
              }
            }
          }
        },
        cta: {
          css: {
            width: 118,
            height: 29
          },
          align: {
            y: {
              type: Align.BOTTOM,
              offset: -37
            }
          },
          txt: {
            fontSize: 17
          }
        }
      }
    })
    View.mainBorder = new MainBorder()
    Control.postMarkup()
    Animation.start()
  }
}
window.Ad = Ad
